
import React from 'react';

const Option = (props) =>{
 
    return (
      <div className='option'>
        <p>{props.count}. {props.optionText} </p>
        <button className='button button--link' onClick= {()=>{
          props.removeOne(props.optionText)
        }}>
         Remove
        </button>
      </div>
    );
  }

  export default Option


